/* Body styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-image: none;
    background-color: cyan;
  }
  
  .container1 {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    padding: 0 20px; /* Adjust padding as needed */
    border-radius: 25px;
  }
  
  .login-box1 {
    display: flex;
    width: 100%;
    max-width: 800px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 25px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: rgba(255, 255, 255, 0.5); /* Transparent background */
    position: relative;
    color: black;
  }
  
  .left-side,
  .right-side {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1;
    padding: 20px;
    position: relative;
    z-index: 1;
  }
  
  .right-side::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(10px);
    z-index: -1;
  }
  
  
  .logo {
    text-align: center;
    position: relative; /* Add position relative to make absolute positioning work */
  }
  
  .logo-text {
    position: absolute; /* Position the text absolutely */
    bottom: 10px; /* Adjust the top value as needed to move the text closer to the logo */
    left: 50%; /* Align the text in the center horizontally */
    transform: translateX(-50%); /* Center the text horizontally */
    font-weight: bolder;
    font-size: 20px;
  }
  
  .logo img {
    max-width: 275px;
    max-height: 400px;
    margin-bottom: 40px; /* Adjust the value as needed to move the image higher */
  }
  
  
  .form-group1{
    margin-bottom: 15px;
  }
  
  .form-group1 label {
    display: block;
    margin-bottom: 5px;
  }
  
  .form-group1 input[type="text"],
  .form-group1 input[type="password"] {
    width: 250px;
    padding: 12px;
    border-radius: 5px;
    border: 2px solid #ccc;
  }
  
  .login-method a {
    color: gray;
    text-decoration: none;
    margin-right: 20px;
  }
  
  .login-method a:hover {
    text-decoration: underline;
  }
  
  .login-method button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 15px 30px; /* Adjust padding as needed */
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
    margin-bottom: 10px;
    font-size: 16px; /* Adjust font size as needed */
    transition: background-color 0.3s ease;
  }
  
  .login-method button:hover {
    background-color: #0056b3;
  }
  
  #loginButton {
    background-color: #3399ff;
    padding: 15px 50px; /* Adjust padding as needed */
    font-size: 16px; /* Adjust font size as needed */
    border-radius: 8px;
    color: #fff;
  }
  
  #loginButton:hover {
    background-color: #0056b3;
    cursor: pointer;
  }
  
  @media screen and (max-width: 768px) {
    .login-box {
        max-width: 100%;
    }
  }
  